<template>
  <div class="page-resume-contact">
    <p class="resume-block-title"></p>
    <el-form label-width="100px" label-position="left" :model="contactInfo" ref="contactInfo"
     :rules="rules">
      <div class="item-block">
        <el-form-item label="" prop="telNumber">
          <el-input placeholder="" v-model="contactInfo.telNumber"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phoneNumber">
          <el-input placeholder="" v-model="contactInfo.phoneNumber"></el-input>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="" prop="email">
          <el-input placeholder="" v-model="contactInfo.email"></el-input>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="" prop="houseAddress">
          <el-input placeholder="" v-model="contactInfo.houseAddress"></el-input>
        </el-form-item>
        <el-form-item label="" prop="houseAddressCode">
          <el-input placeholder="" v-model="contactInfo.houseAddressCode"></el-input>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="" prop="nowAddress">
          <el-input placeholder="" v-model="contactInfo.nowAddress"></el-input>
        </el-form-item>
        <el-form-item label="" prop="nowAddressCode">
          <el-input placeholder="" v-model="contactInfo.nowAddressCode"></el-input>
        </el-form-item>
      </div>

      <div class="item-block col-3">
        <el-form-item label="" prop="emergencyPeople">
          <el-input placeholder="" v-model="contactInfo.emergencyPeople">
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="emergencyPeopleRelation">
          <el-input placeholder="" v-model="contactInfo.emergencyPeopleRelation">
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="emergencyPeoplePhone">
          <el-input placeholder="" v-model="contactInfo.emergencyPeoplePhone">
          </el-input>
        </el-form-item>
      </div>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left" @click="prev"></el-button>
        <el-button type="primary" @click="next('contactInfo')"><i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      var checkPhoneNumber = (rule, value, callback) => {
        if (!value) {
          callback(new Error(''))
        } else if (!/^1[3|4|5|8][0-9]\d{8}$/i.test(value)) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      var checkTelNumber = (rule, value, callback) => {
        if (!value) {
          callback(new Error(''))
        } else if (!/^0\d{2,3}-?\d{7,8}$/i.test(value)) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      var checkEmail = (rule, value, callback) => {
        if (!value) {
          callback(new Error(''))
        } else if (!/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/i.test(value)) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      return {
        contactInfo: {
          telNumber: '',
          phoneNumber: '',
          email: '',
          houseAddress: '',
          houseAddressCode: '',
          nowAddress: '',
          nowAddressCode: '',
          emergencyPeople: '',
          emergencyPeopleRelation: '',
          emergencyPeoplePhone: ''
        },
        rules: {
          telNumber: [
            {validator: checkTelNumber, trigger: 'blur'}
          ],
          phoneNumber: [
            {validator: checkPhoneNumber, trigger: 'blur'}
          ],
          email: [
            {validator: checkEmail, trigger: 'blur'}
          ],
          emergencyPeoplePhone: [
            {validator: checkPhoneNumber, trigger: 'blur'}
          ],
          houseAddress: [
            {required: true, message: '', trigger: 'blur'}
          ],
          houseAddressCode: [
            {required: true, message: '', trigger: 'blur'}
          ],
          emergencyPeople: [
            {required: true, message: '', trigger: 'blur'}
          ],
          nowAddress: [
            {required: true, message: '', trigger: 'blur'}
          ],
          emergencyPeopleRelation: [
            {required: true, message: '', trigger: 'blur'}
          ],
          nowAddressCode: [
            {required: true, message: '', trigger: 'blur'}
          ]
        }
      }
    },
    created () {
      if (sessionStorage.contactInfo) {
        this.contactInfo = JSON.parse(sessionStorage.contactInfo)
      }
    },
    methods: {
      prev () {
        this.$router.push({name: 'position'})
      },
      next (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            sessionStorage.setItem('contactInfo', JSON.stringify(this.contactInfo))
            this.$router.push({name: 'family'})
          }
        })
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-contact
    padding-bottom: 50px
</style>



