<template>
  <div class="page-resume-baseinfo">
    <el-form label-width="80px" label-position="left" :model="baseInfo" ref="baseInfo" :rules="rules">
      <div class="item-block">
        <el-form-item label="" prop="applyPosition">
          <el-select v-model="baseInfo.applyPosition" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="applicationArea">
          <el-select v-model="baseInfo.applicationArea" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="" prop="entryDate">
          <el-date-picker
            v-model="baseInfo.entryDate"
            type="date"
            placeholder=""
            :picker-options="pickerOptions0">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="expectSalary">
          <el-input placeholder="" v-model="baseInfo.expectSalary"></el-input>
        </el-form-item>
      </div>

      <el-form-item label="" prop="channelList">
        <el-checkbox-group v-model="baseInfo.channelList">
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <p class="resume-block-title"></p>
      <div class="item-block">
        <el-form-item label="" prop="name">
          <el-input placeholder="" v-model="baseInfo.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="gender">
          <el-select v-model="baseInfo.gender">
            <el-option v-for="(item, index) in genderOptions"
              :key="index" :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="" prop="birth">
          <el-date-picker
            v-model="baseInfo.birth"
            type="date"
            placeholder=""
            :picker-options="pickerOptions1">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="idNumber">
         <el-input placeholder="" v-model="baseInfo.idNumber"></el-input>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="" prop="height">
         <el-input placeholder="" v-model="baseInfo.height"></el-input>
        </el-form-item>
        <el-form-item label="" prop="weight">
         <el-input placeholder="" v-model="baseInfo.weight"></el-input>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="" prop="address">
         <el-input placeholder="" v-model="baseInfo.address"></el-input>
        </el-form-item>
        <el-form-item label="" prop="politicStatus">
          <el-select v-model="baseInfo.politicStatus">
            <el-option v-for="(item, index) in politicsOptions"
              :key="index" :label="item.label"
              :value="item.label">
            </el-option>
          </el-select>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="" prop="merryStatus">
          <el-radio-group v-model="baseInfo.merryStatus">
            <el-radio
              v-for="(item, index) in checkMarriedList"
              :label=item.label
              :key="index">
              {{ item.label }}
            </el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" label-width="120px" prop="isSoldier">
          <el-radio-group v-model="baseInfo.isSoldier">
            <el-radio
              v-for="(item, index) in soldierList"
              :label="item.label"
              :key="index">
              {{ item.label }}
            </el-radio>
          </el-radio-group>
        </el-form-item>
      </div>
      <div class="item-block btn-container">
        <el-button type="primary"  @click="next('baseInfo')"></el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  import VHeader from './header'

  export default {
    data () {
      var checkIdNumber = (rule, value, callback) => {
        if (!value) {
          callback(new Error(''))
        } else if (!/\d{17}[\d|x]|\d{15}/i.test(value)) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      var checkWeight = (rule, value, callback) => {
        if (!value) {
          callback(new Error(''))
        } else if (!/^[1-9]\d*$/.test(value)) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      var checkHeight = (rule, value, callback) => {
        if (!value) {
          callback(new Error(''))
        } else if (!/^[1-9]\d*$/.test(value)) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      return {
        baseInfo: {
          applyPosition: '',
          applicationArea: '',
          entryDate: '',
          expectSalary: '',
          channelList: [],
          name: '',
          gender: 0,
          birth: '',
          idNumber: '',
          height: '',
          weight: '',
          address: '',
          politicStatus: '',
          merryStatus: '',
          isSoldier: ''
        },
        options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }],
        pickerOptions0: {
          disabledDate (time) {
            return time.getTime() < Date.now()
          }
        },
        pickerOptions1: {
          disabledDate (time) {
            return time.getTime() > Date.now()
          }
        },
        genderOptions: [{
          label: '',
          value: 0
        }, {
          label: '',
          value: 1
        }],
        politicsOptions: [{
          label: ''
        }, {
          label: ''
        }, {
          label: ''
        }],
        checkMarriedList: [
          {label: ''},
          {label: ''},
          {label: ''}
        ],
        soldierList: [
          {label: ''},
          {label: ''}
        ],
        rules: {
          applyPosition: [
            {required: true, message: '', trigger: 'change'}
          ],
          applicationArea: [
            {required: true, message: '', trigger: 'change'}
          ],
          entryDate: [
            {required: true, message: '', trigger: 'change', type: 'date'}
          ],
          expectSalary: [
            {required: true, message: '', trigger: 'blur'}
          ],
          channelList: [
            {required: true, message: '', trigger: 'change', type: 'array'}
          ],
          name: [
            {required: true, message: '', trigger: 'blur'}
          ],
          birth: [
            {required: true, message: '', trigger: 'change', type: 'date'}
          ],
          idNumber: [
            {validator: checkIdNumber, trigger: 'blur'}
          ],
          height: [
            {validator: checkHeight, trigger: 'blur'}
          ],
          weight: [
            {validator: checkWeight, trigger: 'blur'}
          ],
          address: [
            {required: true, message: '', trigger: 'blur'}
          ],
          politicStatus: [
            {required: true, message: '', trigger: 'change'}
          ],
          merryStatus: [
            {required: true, message: '', trigger: 'change'}
          ],
          isSoldier: [
            {required: true, message: '', trigger: 'change'}
          ]
        }
      }
    },
    created () {
      if (sessionStorage.baseInfo) {
        var baseInfo = JSON.parse(sessionStorage.baseInfo)
        baseInfo.birth = new Date(baseInfo.birth)
        baseInfo.entryDate = new Date(baseInfo.entryDate)
        this.baseInfo = baseInfo
      }
    },
    methods: {
      next (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log(this.baseInfo)
            sessionStorage.setItem('baseInfo', JSON.stringify(this.baseInfo))
            this.$router.push({name: 'position'})
          }
        })
      }
    },
    components: { VHeader }
  }
</script>

<style lang="stylus">
.page-resume-baseinfo
  padding-bottom: 50px
</style>



