<template>
  <div class="page-resume-family">
    <p class="resume-block-title"></p>
    <el-form label-width="80px" label-position="left" class="demo-dynamic">
      <div class="form-item-group"
        v-for="(item, index) in familyMembers"
        :key="item.key">
        <div class="item-block">
          <el-form-item label="">
            <el-input v-model="item.name" placeholder="" @blur="validate(item.name, '')">
            </el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="item.relation" placeholder="" @blur="validate(item.relation, '')"></el-input>
          </el-form-item>
        </div>

         <div class="item-block">
          <el-form-item label="">
            <el-input v-model.number="item.age" placeholder="" @blur="validate(item.age, '')"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="item.address" placeholder="" @blur="validate(item.address, '')"></el-input>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="">
            <el-input v-model="item.workPlace" placeholder="" @blur="validate(item.workPlace, '')"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="item.position" placeholder="" @blur="validate(item.position, '')"></el-input>
          </el-form-item>
        </div>
      </div>
      <div class="item-block item-add">
        <el-button @click="addMembers" icon="el-icon-plus"></el-button>
      </div>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left" @click="prev"></el-button>
        <el-button type="primary" @click="next('familyMembers')"><i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        familyMembers: [{
          name: '',
          relation: '',
          age: '',
          address: '',
          workPlace: '',
          position: ''
        }]
      }
    },
    created () {
      if (sessionStorage.familyMembers) {
        this.familyMembers = JSON.parse(sessionStorage.familyMembers)
      }
    },
    methods: {
      addMembers () {
        if (this.familyMembers.length < 3) {
          this.familyMembers.push({
            name: '',
            relation: '',
            age: '',
            address: '',
            workPlace: '',
            position: ''
          })
        } else {
          this.$message('3')
        }
      },
      validate (item, value) {
        if (!item) {
          this.$message('' + value)
          return
        }
        if (value === '') {
          if (!/^[1-9]\d*$/.test(item)) {
            this.$message('')
          } else {
            if (item < 18 || item > 100) {
              this.$message('18100')
            }
          }
        }
      },
      prev () {
        this.$router.push({name: 'contact'})
      },
      next () {
        for (let i = 0; i < this.familyMembers.length; i++) {
          for (let item in this.familyMembers[i]) {
            if (!this.familyMembers[i][item]) {
              this.$message('')
              return
            }
          }
        }
        sessionStorage.setItem('familyMembers', JSON.stringify(this.familyMembers))
        this.$router.push({name: 'education'})
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-family
    padding-bottom: 50px
</style>



