<template>
  <div class="page-resume-education">
    <p class="resume-block-title"></p>
    <el-form label-width="80px" label-position="left">
      <div class="form-item-group"
        v-for="(item, index) in educationList" :key="index">
        <div class="item-block">
          <el-form-item label="">
            <el-date-picker
              v-model="item.startDate"
              type="date"
              @change="validate(item.startDate, '')"
              placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker
              v-model="item.endDate"
              type="date"
              @change="validate(item.endDate, '')"
              placeholder="">
            </el-date-picker>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="">
            <el-input v-model="item.schoolName" placeholder=""
            @blur="validate(item.schoolName, '')"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="item.majorName" placeholder=""
            @blur="validate(item.majorName, '')"></el-input>
          </el-form-item>
        </div>

        <el-form-item label="">
          <el-select v-model="item.xl" @change="validate(item.xl, '')">
            <el-option v-for="(item, index) in educationOptions"
              :key="index" :label="item.label" :value="item.label"></el-option>
          </el-select>
        </el-form-item>
      </div>
      <div class="item-block item-add">
        <el-button @click="addList" icon="el-icon-plus"></el-button>
      </div>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left" @click="prev"></el-button>
        <el-button type="primary" @click="next"><i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        educationList: [{
          startDate: '',
          endDate: '',
          schoolName: '',
          majorName: '',
          xl: ''
        }],
        educationOptions: [{
          label: ''
        }, {
          label: ''
        }, {
          label: ''
        }, {
          label: ''
        }, {
          label: ''
        }, {
          label: ''
        }]
      }
    },
    created () {
      if (sessionStorage.educationList) {
        var educationList = JSON.parse(sessionStorage.educationList)
        for (let i = 0; i < educationList.length; i++) {
          educationList[i].startDate = new Date(educationList[i].startDate)
          educationList[i].endDate = new Date(educationList[i].endDate)
        }
        this.educationList = educationList
      }
    },
    methods: {
      addList () {
        if (this.educationList.length < 3) {
          this.educationList.push({
            startDate: '',
            endDate: '',
            schoolName: '',
            majorName: '',
            xl: ''
          })
        } else {
          this.$message('3')
        }
      },
      validate (item, value) {
        if (!item) {
          console.log(item)
          this.$message('' + value)
        }
      },
      prev () {
        this.$router.push({name: 'family'})
      },
      next () {
        for (let i = 0; i < this.educationList.length; i++) {
          for (let item in this.educationList[i]) {
            if (!this.educationList[i][item]) {
              console.log(item)
              this.$message('')
              return
            }
          }
          if (this.educationList[i].startDate.getTime() >= this.educationList[i].endDate.getTime()) {
            this.$message('')
            return
          }
        }
        sessionStorage.setItem('educationList', JSON.stringify(this.educationList))
        this.$router.push({name: 'work'})
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-education
    padding-bottom: 50px
</style>



