<template>
  <div class="page-resume-work">
    <p class="resume-block-title">5</p>
    <el-form label-width="85px" label-position="left">
      <div class="form-item-group"
        v-for="(item, index) in workList" :key="index">
        <div class="item-block">
          <el-form-item label="">
            <el-date-picker
              v-model="item.startDate"
              type="date"
              placeholder=""
              @change="validate(item.startDate, '')">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker
              v-model="item.endDate"
              type="date"
              placeholder=""
              @change="validate(item.endDate, '')"
              >
            </el-date-picker>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="">
            <el-input v-model="item.workPlace" placeholder="" @blur="validate(item.workPlace, '')"></el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-input v-model="item.position" placeholder="" @blur="validate(item.position, '')"></el-input>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="">
            <el-input v-model="item.salary" placeholder="" @blur="validate(item.salary, '')"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="item.leaveReason" placeholder="" @blur="validate(item.leaveReason, '')"></el-input>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="">
            <el-input v-model="item.proveName" placeholder="" @blur="validate(item.proveName, '')"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="item.proveTel" placeholder="" @blur="validate(item.proveTel, '')"></el-input>
          </el-form-item>
        </div>
      </div>
      <div class="item-block item-add">
        <el-button @click="addList" icon="el-icon-plus"></el-button>
      </div>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left" @click="prev"></el-button>
        <el-button type="primary" @click="next"><i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        workList: [{
          startDate: '',
          endDate: '',
          workPlace: '',
          position: '',
          salary: '',
          leaveReason: '',
          proveName: '',
          proveTel: ''
        }]
      }
    },
    created () {
      if (sessionStorage.workList) {
        var workList = JSON.parse(sessionStorage.workList)
        for (let i = 0; i < workList.length; i++) {
          workList[i].startDate = new Date(workList[i].startDate)
          workList[i].endDate = new Date(workList[i].endDate)
        }
        this.workList = workList
      }
    },
    methods: {
      validate (item, value) {
        if (!item) {
          this.$message('' + value)
          return
        }
        if (value === '') {
          if (!/^1[3|4|5|8][0-9]\d{8}$/i.test(value)) {
            this.$message('')
          }
        }
      },
      addList () {
        if (this.workList.length < 3) {
          this.workList.push({
            startDate: '',
            endDate: '',
            workPlace: '',
            position: '',
            salary: '',
            leaveReason: '',
            proveName: '',
            proveTel: ''
          })
        } else {
          this.$message('3')
        }
      },
      prev () {
        this.$router.push({name: 'education'})
      },
      next () {
        for (let i = 0; i < this.workList.length; i++) {
          for (let item in this.workList[i]) {
            if (!this.workList[i][item]) {
              this.$message('')
              return
            }
          }
          if (this.workList[i].startDate.getTime() >= this.workList[i].endDate.getTime()) {
            this.$message('')
            return
          }
          if (!/^1[3|4|5|8][0-9]\d{8}$/i.test(this.workList[i].proveTel)) {
            this.$message('')
            return
          }
        }
        sessionStorage.setItem('workList', JSON.stringify(this.workList))
        this.$router.push({name: 'technology'})
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-work
    padding-bottom: 50px
</style>



