<template>
  <div class="page-resume-technology">
    <p class="resume-block-title"></p>
    <el-form :model="skill" ref="skill">
      <el-form-item label="?EXCEL,WORD,PPT" 
      prop="software"
      :rules="[
        {required: true, message: ''}
      ]">
        <!-- <el-checkbox-group v-model="skill.software.softwareList">
          <el-checkbox label="EXCEL"></el-checkbox>
          <el-checkbox label="WORD"></el-checkbox>
          <el-checkbox label="PPT"></el-checkbox>
        </el-checkbox-group> -->
        <el-input type="textarea" placeholder="" v-model="skill.software">
        </el-input>
      </el-form-item>

      <el-form-item label=" ?" prop="training"
        :rules="[
          {required: true, message: ''}
        ]"
        >
        <el-input :autosize="textareaSize" type="textarea" placeholder="" 
        v-model="skill.training"></el-input>
      </el-form-item>

      <el-form-item label=" / " prop="languageLevel"
        :rules="[
          {required: true, message: ''}
        ]"
        >
        <el-input :autosize="textareaSize" type="textarea" placeholder=""
        v-model="skill.languageLevel"></el-input>
      </el-form-item>

      <el-form-item label="" prop="interest"
        :rules="[
          {required: true, message: ''}
        ]"
        >
        <el-input :autosize="textareaSize" type="textarea" placeholder=""
        v-model="skill.interest"></el-input>
      </el-form-item>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left" @click="prev"></el-button>
        <el-button type="primary" @click="next('skill')"><i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        skill: {
          software: '',
          training: '',
          languageLevel: '',
          interest: ''
        },
        textareaSize: { minRows: 5, maxRows: 8 }
      }
    },
    created () {
      if (sessionStorage.skill) {
        this.skill = JSON.parse(sessionStorage.skill)
      }
    },
    methods: {
      prev () {
        this.$router.push({name: 'work'})
      },
      next (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            sessionStorage.setItem('skill', JSON.stringify(this.skill))
            this.$router.push({name: 'shengao'})
          }
        })
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-technology
    padding-bottom: 50px
</style>



