<template>
  <div class="page-user-interview">
    <div class="quickly-enter">
      <el-dropdown>
        <el-button size="small" type="primary" plain>
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item></el-dropdown-item>
          <el-dropdown-item></el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <div class="form-block-title">
      <h3></h3>
    </div>
    <form-card></form-card>
    <div class="btn-container">
      <el-button icon="el-icon-upload2" type="primary" size="small"></el-button>
    </div>
    <el-table
      ref="multipleTable"
      :data="tableData3"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
        width="80">
      </el-table-column>
      <el-table-column
        label=""
        width="50">
        <template slot-scope="scope">{{ scope.row.gender }}</template>
      </el-table-column>
      <el-table-column
        prop="post"
        label=""
        width="80"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="date_of_birth"
        label=""
        width="100"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="marriage"
        label=""
        width="80"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="phone"
        label=""
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="interviewDate"
        label=""
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="majorName"
        label=""
        width="80"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="result"
        label=""
        width="80"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        fixed="right"
        label=""
        width="250">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
          <el-button type="text" size="small"></el-button>
          <el-button type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination-container">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="100"
        layout="total, prev, pager, next"
        :total="1000">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import formCard from './form-card'

  export default {
    data () {
      return {
        tableData3: [{
          gender: '',
          name: '',
          address: ' 1518 ',
          position: '',
          birthday: '1990/11/11',
          married: '',
          telphone: '151 2681 1895',
          interviewTime: '2017/08/08',
          majorName: '',
          result: ''
        }, {
          gender: '',
          name: '',
          address: ' 1518 ',
          position: '',
          birthday: '1990/11/11',
          married: '',
          telphone: '151 2681 1895',
          interviewTime: '2017/08/08',
          majorName: '',
          result: ''
        }, {
          gender: '',
          name: '',
          address: ' 1518 ',
          position: '',
          birthday: '1990/11/11',
          married: '',
          telphone: '151 2681 1895',
          interviewTime: '2017/08/08',
          majorName: '',
          result: ''
        }, {
          gender: '',
          name: '',
          address: ' 1518 ',
          position: '',
          birthday: '1990/11/11',
          married: '',
          telphone: '151 2681 1895',
          interviewTime: '2017/08/08',
          majorName: '',
          result: ''
        }],
        multipleSelection: [],
        currentPage: 1
      }
    },
    methods: {
      toggleSelection (rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row)
          })
        } else {
          this.$refs.multipleTable.clearSelection()
        }
      },
      handleSelectionChange (val) {
        this.multipleSelection = val
      },
      handleClick (row) {
        console.log(row)
      },
      handleSizeChange (val) {
        console.log(` ${val} `)
      },
      handleCurrentChange (val) {
        console.log(`: ${val}`)
      },
      handleSelect (key, keyPath) {
        console.log(key, keyPath)
      }
    },
    components: { formCard }
  }
</script>

<style lang="stylus">
  .page-user-interview
    padding-bottom: 30px
    .form-block-title
      padding-bottom: 15px
      margin-bottom: 10px
      border-bottom: 1px solid #ddd
      h3
        padding-bottom: 10px
        font-size: 16px
        font-weight: 500
    .interview-el-menu
      display: inline-block
    .btn-container
      padding: 10px 0 15px
      text-align: right
    .pagination-container
      padding-top: 30px
      text-align: center
    .el-menu--horizontal,
    .el-menu--horizontal>.el-submenu.is-active .el-submenu__title
      border-bottom: 0
    .el-menu--horizontal .el-submenu>.el-menu
      text-align: left
      width: 100%
    .quickly-enter
      padding-top: 25px
      display: flex
      align-items: center
      justify-content: flex-end
      .el-button
        margin-left: 15px
</style>



